import React, { useState } from "react";

import "./Home.css";
import heartIcon from "../images/heart.png";
import ImgGallery from "./ImgGallery.jsx";

export default function Home() {
   const [heartHovered, setHeartHovered] = useState();
   const [heartClicked, setHeartClicked] = useState(false);

   const overlayClass = () => {
      if (heartHovered == null) return "";
      else if (heartHovered === false) return "overlay-not-hovered";
      else return "overlay-hovered";
   };
   const textClass = () => {
      if (heartHovered == null) return "";
      else if (heartHovered === false) return " reveal-delay ";
      else return " hide ";
   };
   const namesClass = () => {
      if (heartHovered === true) return " reveal-delay ";
      else if (heartHovered === false) return " hide ";
      else return "";
   };

   return (
      <>
         <ImgGallery heartClicked={heartClicked} />
         <div className={" home " + (heartClicked ? " hidden " : "")}>
            <div className={"overlay-left " + overlayClass()}></div>
            <div className={"overlay-right " + overlayClass()}></div>
            <div className={" text " + textClass()}>
               <p className="noSelect">21.12.2017</p>
            </div>
            <div
               className="heart"
               onMouseOver={() => setHeartHovered(true)}
               onMouseOut={() => setHeartHovered(false)}
               onClick={() => setHeartClicked(true)}
            >
               <img className="heart-icon" src={heartIcon} alt="" />
            </div>
            <div className={" text " + textClass()}>
               <p className="noSelect">21.12.2020</p>
            </div>
            <div className={" names " + namesClass()}>
               <div className="iulia">
                  <p>Iulia</p>
               </div>
               <div className="vlad">
                  <p>Vlad</p>
               </div>
            </div>
         </div>
      </>
   );
}
