import React, { useState } from "react";
import "./ImgGallery.css";
import img1 from "../images/gallery/1.jpg";
import img2 from "../images/gallery/2.jpg";
import img3 from "../images/gallery/3.jpg";
import img4 from "../images/gallery/4.jpg";
import img5 from "../images/gallery/5.jpg";
import img6 from "../images/gallery/6.jpg";
import img7 from "../images/gallery/7.jpg";
import img8 from "../images/gallery/8.jpg";

export default function ImgGallery({ heartClicked }) {
   const [bigImg, setBigImg] = useState();
   const [showText, setShowText] = useState(true);
   const [smallImgHovered, setSmallImgHovered] = useState();
   const [imgWasClicked, setImgWasClicked] = useState(false);

   const bigImgClass = () => {
      if (showText) return " disappear ";
      if (imgWasClicked) return " appear ";
      return "";
   };
   const textClass = () => {
      let ans = "";
      if (bigImg == null) ans += " show ";
      else ans += " not-show ";
      if (!showText) ans += " disappear ";
      if (showText) ans += " appear ";

      return ans;
   };

   const imgs = [img1, img2, img3, img4, img5, img6, img7, img8];
   const curText = () => {
      if (bigImg == null)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Hei, cu siguranta cred ca ai observat ca am incercat sa fac
                  ceva diferit de data asta :)))
               </p>
               <p className="gallery-text">
                  {" "}
                  Sper ca nu te deranjeaza ca nu ti-am facut o scrisoare fizica
                  si sa stii ca am muncit mult mai mult la chestia asta decat as
                  fi facut pentru aia :)
               </p>
               <p className="gallery-text">
                  {" "}
                  Oricum, dupa multe momente frumoase, dar si multe probleme si
                  certuri, am reusit sa implinim 3 ani de relatie si sunt foarte
                  fericit.
               </p>
               <p className="gallery-text">
                  La inceput chiar nu ma asteptam sa ajungem pana aici, nici
                  macar nu imi puneam problema si doar speram sa ma simt bine
                  alaturi de tine cat timp suntem impreuna, dar iata ca pana la
                  urma m-am atasat mult de tine si acum nu imi mai pot imagina
                  viata fara sa fii si tu acolo.
               </p>
            </div>
         );
      if (bigImg === img1)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Unele dintre cele mai frumoase amintiri pe care le avem
                  impreuna sunt zilele de vara de la inceputul relatiei cand
                  stateam pe afara non-stop, chiar daca nu faceam ceva anume,
                  doar ne simteam bine impreuna. Totusi, cu timpul ne-am mai
                  maturizat si nu mai iesim chiar in fiecare zi, deoarece am
                  realizat ca nu functioneaza asta pe termen lung.
               </p>
            </div>
         );
      else if (bigImg == img2)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  {
                     "De asemenea, imi place mult atunci cand vin la tine / tu la mine si doar stam in pat si ne iubim (uneori si adormim din greseala :)) ), ceea ce nu se intampla prea des totusi :<"
                  }{" "}
               </p>
            </div>
         );
      else if (bigImg == img3)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Voiam sa iti mai zic si ca imi place mult sa facem chestii
                  impreuna (daca nu e vorba de vazut video-urile lui balaban) si
                  ca mi-ar placea ca pe viitor sa avem in program o parte din
                  timp dedicata unor activitati pe care sa le facem doar noi.{" "}
               </p>
            </div>
         );
      else if (bigImg == img4)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Desi in timpul pandemiei ne este mai greu sa ne vedem, de
                  obicei tot ne descurcam cumva sa stam impreuna, chiar daca
                  asta presupune uneori incalcarea mai multor legi (asta e,
                  macar stam impreuna) :)))
               </p>
            </div>
         );
      else if (bigImg == img5)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Desigur, nu pot sa uit de toate ocaziile cand am vizitat multe
                  orase, mai mult sau mai putin dubioase (Uricani), cand ne-am
                  distrat si ne-am simtit bine, dar si cand am trecut prin ploi
                  torentiale, cazari dubioase si trenuri cu temperaturi extrem
                  de mari.{" "}
               </p>
            </div>
         );
      else if (bigImg == img6)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Evident ca o sa continuam sa vizitam alte orase, dar sper
                  totusi ca nu vom mai repeta greselile din datile trecute si sa
                  ne simtim si mai bine decat inainte :))))
               </p>
            </div>
         );
      else if (bigImg == img7)
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  Acestea fiind spuse, voiam sa iti mai amintesc inca o data ca
                  nu mi-as dori niciodata sa ne despartim, ca o sa iti fiu
                  intotdeauna alaturi, chiar daca nu gasesc mereu cuvintele
                  potrivite care sa te faca sa te simti mai bine, ca esti cea
                  mai importanta persoana din viata mea si, desi am niste dubii
                  despre reciprocitatea acestui sentiment, sper sa ma insel si
                  sa fie doar in imaginatia mea, avand in vedere circumstantele
                  in care iti scriu asta.
               </p>
            </div>
         );
      else
         return (
            <div className="gallery-text-wrapper">
               <p className="gallery-text">
                  In orice caz, daca am fi avut un copil din momentul in care am
                  ajuns impreuna, acum cu siguranta ar fi stiut sa vorbeasca si
                  sper sa ajunga sa aiba voie sa bea alcool si multe alte
                  chestii :))) Te pup :*
               </p>
            </div>
         );
   };

   return (
      <div className={" img-gallery " + (heartClicked ? "" : " hidden ")}>
         <div className="small-imgs">
            {imgs.map((img, idx) => (
               <img
                  className={
                     " noSelect " + (smallImgHovered === idx ? " hovered " : "")
                  }
                  key={idx}
                  src={img}
                  alt=""
                  onMouseEnter={() => {
                     setSmallImgHovered(idx);
                  }}
                  onMouseLeave={() => {
                     setSmallImgHovered(null);
                  }}
                  onClick={() => {
                     setImgWasClicked(true);
                     setTimeout(() => setImgWasClicked(false), 500);
                     setShowText(false);
                     setBigImg(img);
                  }}
               />
            ))}
         </div>
         <div
            className="big-img"
            onClick={() => {
               if (bigImg == null) return;
               setImgWasClicked(true);
               setTimeout(() => setImgWasClicked(false), 500);
               setShowText(!showText);
            }}
         >
            <div className={" img-text noSelect " + textClass()}>
               {curText()}
            </div>
            <img className={" noSelect " + bigImgClass()} src={bigImg} alt="" />
         </div>
      </div>
   );
}
